@import url('https://fonts.googleapis.com/css2?family=Anek+Malayalam:wght@300;400&family=Lato:wght@300;400;700&family=Montserrat+Alternates:ital,wght@0,400;0,500;0,600;0,900;1,900&family=Raleway:wght@300;400;500;600&display=swap');

body{
  margin: 0;
  padding: 10px;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.custom-cursor {
  cursor: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="20" height="20"><circle cx="10" cy="10" r="10" fill="white" /><circle cx="10" cy="10" r="5" fill="grey" /><circle cx="10" cy="10" r="10" fill="black" style="opacity: 0.2" /></svg>'), auto;
  transition: cursor 0.2s ease-in-out;
}


.main-section{
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: rgb(255, 255, 255);
  height: 90vh;
}

header{
  margin: 10px 5px;
  display: flex;
  text-align: center;
  font-family: 'Anek Malayalam', sans-serif;
  font-size: 32px;
  letter-spacing: 1px;
  background: #ffffff;
  color: #13274F;
  text-shadow: 2px 2px 2px #c4c4c4;
  justify-content: space-around;
  align-items: center;
  text-decoration: none;
}

.menu-list > ul {
  display: flex;
  align-items: center;
  list-style: none;
}

Link{
  text-decoration: none;
  color: inherit;
  
}

 li{
  padding: 15px;
  line-height: 20px;
  font-size: 26px;
  border-radius: 20px;
}

 li:hover{
  background-color:#13274F ;
  color: white;
}

.content{
  padding:  10px 40px;
  display: flex;
  align-items: center;
  text-align: center;
}

.contact-icons{
  display: flex;
  align-items: center;
  justify-content: center;
}

.hello{
  font-size: 68px;
  padding-top: 30px;
  padding-bottom: 50px;
  color: #3F3D56;
}

.dots{
  font-size: 95px;
}

.name{
  font-family: 'Lato', sans-serif;
  font-weight: 600;
  color: #3e3c5f;
  font-size: 28px;
}

.content > p{
  font-family: 'Raleway', sans-serif;
  font-size: 25px;
  font-weight: 600;
  color: #302e41;
  line-height: 45px;
  text-align: center;
}

.right-section{
  padding: 8px;
}

.section-heading > h1{
  font-family: 'Raleway', sans-serif;
  font-size: 45px;
  font-weight: 600;
  color: #302e41;
  line-height: 45px;
  text-align: center;
  padding-bottom: 45px;
  text-decoration: underline;
}

.contact-icons{
  height: 100px;

}

.icons{
  height: 50px;
  width: 80px;
}

.icons:hover{
  height: 55px;
}

.about-section{
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 30px;
  height: auto;
}

.right-setion > p{
  font-family: 'Lato', sans-serif;
  font-size: 25px;
  font-weight: 600;
  color: #452c63;
  line-height: 45px;
}

.extra > p{
  font-family: 'Lato', sans-serif;
  font-size: 25px;
  font-weight: 600;
  color: #452c63;
  line-height: 45px;
}



.skills > svg{
  height: 65px;
  margin-top: 8px;
  cursor: none;
}

.readmoreBtn{
  font-size: 22px;
  height: 40px;
  width: auto;
  padding: 10px;
  color: #3F3D56;
  background: none;
  border: none;
  text-align: center;
  border-bottom: 2px solid #452c63;
}

.readmoreBtn:hover{
  color: #3F3D56;
  font-size: 23px;
}


.skillSection{
  display: flex;
  align-items: center;
}

.section-sub-title{
  font-family: 'Raleway', sans-serif;
  font-size: 30px;
  font-weight: 600;
  color: #452c63;
  line-height: 45px;
  text-align: center;
  padding:50px ;
}

.skill-icons{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}


.skill-des{
  margin-top: 10px;
  padding-left: 20px;
}

.skill-des > p{
  font-family: 'Lato', sans-serif;
  font-size: 20px;
  font-weight: 600;
  color: #452c63;
  line-height: 35px;
  padding-bottom: 5px;
}

.hero-section{
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

.left > h2{
  font-family: 'Raleway', sans-serif;
  font-size: 52px;
  font-weight: 600;
  color: #13274F;
  line-height: 45px;
  padding: 10px 5px;
}

.left >p {
  font-family: 'Lato', sans-serif;
  font-size: 25px;
  font-weight: 600;
  color: #452c63;
  line-height: 30px;
  line-height: 40px;
  padding-bottom: 10px 5px
}

.edu{
  height: auto;
  padding: 4px;
  border-radius: 10px;
  border: 2px solid grey ;
  margin: 15px 60px;
}

.edu-header{
  display: flex;
  align-items: center;
  justify-content: space-around;
  padding: 15px 5px;
}

.edu-logo{
  height: 110px;
  width: 110px;
  overflow: hidden;
  border: 2px solid grey;
  border-radius: 50%;
  background-image: url(./assets//education/Sahrdaya-logo.png);
  background-position: center;
  background-size: contain;
}

.edu-details{
  font-family: 'Lato', sans-serif;
  font-size: 20px;
  font-weight: 600;
  color: #452c63;
  line-height: 30px;
  padding-bottom: 5px;
}

.edu-des{
  padding-top: 10px;
  border-top: 2px solid #3e3c5f;
}

.edu-name{
  font-family: 'Raleway', sans-serif;
  font-size: 45px;
  font-weight: 600;
  color: #302e41;
  line-height: 45px;
  padding: 10px 5px;
}

.course-info > h4{
  font-family: 'Raleway', sans-serif;
  font-size: 30px;
  font-weight: 500;
  color: #3F3D56;
}

.course-info > h4 > span{
  font-family: 'Raleway', sans-serif;
  font-size: 21px;
  font-weight: 500;
  color: #3f3d52;
}


@media only screen and (max-width: 600px) {
    body{
      height: auto;
      width: 100%;
    }

    header{
      width: 100%;
    }

    *{
      margin: 0;
      padding: 0;
      box-sizing: border-box;
    }

    header > h1{
      font-family: 'Anek Malayalam', sans-serif;
      font-size: 48px;
      letter-spacing: 1px;
      background: #ffffff;
      color: #13274F;
      text-shadow: 2px 2px 2px #c4c4c4;
      text-align: center;
    }

    li{
      display: none;
    }



    .main-section , .hero-section{
      display: flex;
      flex-direction: column;
    }

    .main-section{
      height: auto;
    }

    .rightHeroSection > svg{
      height: 200px;
    }

    .section-heading{
      
    }
    
}








